	module.exports = {"data":{"site":{"siteMetadata":{"rootURL":"https://naoty.github.io"}},"markdownRemark":{"frontmatter":{"title":"zshに戻った","time":"2018-05-16T21:34:00+0900"},"html":"<p>1年くらい前からシェルはfishを使っていたけど、最近zshに戻ってきた。理由としては、当時はやってなかったインフラの作業がメインの業務になり、シェルスクリプトを書くようになったからだ。fishはbashとの互換性がないので、bashを書くときに頭を切り替えるのが少しずつストレスになってきた。</p>\n<p>fishを使っていた頃は<a href=\"https://github.com/fisherman/fisherman\">fisherman</a>というプラグインマネージャーを使っていたけど、zshでもプラガブルな設定は続けていきたいので<a href=\"https://github.com/zplug/zplug\">zplug</a>を使い始めた。</p>\n<p>相変わらずzshのプラグインはいい感じのものがないので、またも自分で<a href=\"https://github.com/naoty/prompt\">プロンプト</a>を書くことになった。世の中の人気のあるプロンプトのテーマは余計な情報が多くて重いので、結局いつも自分で書くことになっている。<a href=\"https://github.com/sindresorhus/pure\">pure</a>はよさそうだったけど、二列に慣れず惜しくも採用できなかった。</p>","fileAbsolutePath":"contents/posts/30.md"}},"pathContext":{"id":"contents/posts/30.md absPath of file >>> MarkdownRemark"}}

